/* Reset and Base Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html, body {
    height: 100%;
    font-family: "Courier", monospace;
    color: #ECDFCC;
    background-color: #1E201E;
    font-size: 16px;
  }
  
  /* Main Container - Full height centering */
  .home-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 40px 20px;
  }
  
  /* Avatar/Logo placeholder */
  .home-avatar {
    width: 80px;
    height: 80px;
    background-color: #ECDFCC;
    margin: 0 auto 30px auto;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: #1E201E;
    font-weight: bold;
  }
  
  /* Main heading */
  .home-name {
    font-size: 2.5rem;
    font-weight: 300;
    margin-bottom: 15px;
    color: #ECDFCC;
    letter-spacing: 1px;
  }
  
  /* Tagline */
  .home-tagline {
    font-size: 1rem;
    color: #697565;
    margin-bottom: 40px;
    font-weight: 300;
  }
  
  /* Navigation Menu */
  .home-nav {
    margin-bottom: 40px;
  }
  
  .home-nav-item {
    display: block;
    padding: 8px 0;
    color: #ECDFCC;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.2s ease;
    position: relative;
  }
  
  .home-nav-item:hover {
    color: #697565;
  }
  
  .home-nav-item::before {
    content: "â†’ ";
    opacity: 0;
    transition: opacity 0.2s ease;
    position: absolute;
    left: -25px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .home-nav-item:hover::before {
    opacity: 1;
  }
  
  /* Social Links */
  .home-social {
    // border: 1px solid #697565;
    // border-radius: 6px;
    padding: 12px 20px;
    display: flex;
  }
  
  .home-social-link {
    color: #697565;
    text-decoration: none;
    margin: 0 15px;
    font-size: 0.9rem;
    transition: color 0.2s ease;
  }
  
  .home-social-link:hover {
    color: #ECDFCC;
  }
  
  /* Content area for pages */
  .home-content {
    max-width: 600px;
    text-align: left;
    margin-top: 30px;
  }
  
  .home-content h2 {
    color: #ECDFCC;
    font-size: 1.5rem;
    margin-bottom: 15px;
    font-weight: 400;
  }
  
  .home-content p {
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  .home-content a {
    color: #697565;
    text-decoration: underline;
  }
  
  .home-content a:hover {
    color: #ECDFCC;
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .home-name {
      font-size: 2rem;
    }
    
    .home-container {
      padding: 20px 15px;
    }
    
    .home-avatar {
      width: 60px;
      height: 60px;
      font-size: 20px;
    }
  }